@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
  font-family: 'Times New Roman', Times, serif;
}

button {
  font: inherit;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
  border-radius: 6px;
  background-color: transparent;
  color: #1a8ed1;
  border: 1px solid #1a8ed1;
}

button:hover, button:active {
  background-color: #1ac5d1;
  border-color: #1ac5d1;
  color: white;
}
.LoadingSpinner_spinner__A1Kx5 {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.LoadingSpinner_spinner__A1Kx5:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid teal;
  border-color: teal transparent teal transparent;
  -webkit-animation: LoadingSpinner_spinner__A1Kx5 1.2s linear infinite;
          animation: LoadingSpinner_spinner__A1Kx5 1.2s linear infinite;
}
@-webkit-keyframes LoadingSpinner_spinner__A1Kx5 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes LoadingSpinner_spinner__A1Kx5 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
